.section-inbox {
  background-color: #fff59b;
  height: calc(100vh - 155px);
  display: grid;
  justify-content: center;
  align-content: flex-start;
  gap: 13px;
  padding: 25px 22px;
}

.section-inbox__title {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-size: 60px;
  font-weight: 400;
}

.section-inbox__mail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 334px;
  height: 73px;
  background-color: #fffde8;
  border: solid 1px #000000;
  padding: 22px 18px;
}

.section-inbox__mailsubject {
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: 400;
  margin: 0;
}

.buttons__inbox {
  background-color: #393939;
}
